import opg from 'ts/opg.ts';
import Panel from 'ts/ui/Panel.ts';
import Table from 'ts/ui/Table.ts';
import {Combo} from 'ts/ui/Combo';
import {Languages} from 'ts/util/Languages';
import {store} from '../../../ts/util/store';
import PopUp from "../../../ts/ui/Popup";
import {url} from "../../../ts/util/utils";



opg.api({
    'getcateringlisttotalsumbyuser!post': 'Operation/Getcateringlisttotalsumbyuser',
	'getcateringlistbyuser!post': 'Operation/Getcateringlistbyuser',
    'addCatering!post':'Operation/AddCatering'
});

const lpg = Languages.package;

const form = $('#tbSearch');


let clng='en';

if(localStorage.getItem("lngPkg")!=null|| localStorage.getItem("lngPkg")!=undefined)
{
    const currentlng=localStorage.getItem("lngPkg");
    clng=currentlng.replace(/["""]/g,"");
}


$("#totalsum").html(lpg.totalsum + ":0");

// let panel: Panel = opg.wrapPanel('#tbSearch', {
// 	title: `${lpg.searchService}`,
// 	btnSearchText: `<i class="ico-find"></i> ${lpg.search}`,
// 	btnSearchClick: function () {
//         refreshtable();
// 	}
// });

function refreshtable() {
    let xparam = $('#tbSearch').fieldsToJson();
    xparam.pageIndex = 0;
    xparam.currentlng=clng;
    xparam.reservetype="catering";
    tb.update(xparam);
    opg.api.getcateringlisttotalsumbyuser(xparam , (data)=>{
        $("#totalsum").html(lpg.totalsum + ":" + data[0].totalsum);
    });


    setTimeout(function () {
        var trl=$("#tb").find("tr").length;

        if(trl>2) {
            $('#btnServiceApply').attr('disabled',"true");
        }
        else
        {
            $('#btnServiceApply').removeAttr("disabled");
        }

    }, 1000);

}


//const permission = store.get('permission');
let tbButtons = [];
// tbButtons.push({id: 'btnServiceApply', className: 'btn-warning', html: `${lpg.serviceApply}`});
let tb: Table = opg('#tb').table({
	api: opg.api.getcateringlistbyuser,
	lazy: true,
	titleBar: {
		title: `${lpg.list}`,
		buttons: tbButtons,
	},
	columns: [
		{
			text: `${lpg.standno}`, width: 100,
			src: 'standno',
		},
		{
			text: `${lpg.exhibitor}`, width: 100,
			src: 'exhibitor',
		},
		{
			text: `${lpg.company}`, width: 100,
			src: 'company',
		},
        {
            text: `${lpg.reserveitemno}`, width: 100,
            src: 'reserveitemno',
        },
        {
            text: `${lpg.itemname}`, width: 100,
            src: 'itemname',
        },
        {
            text: `${lpg.description}`, width: 100,
            src: 'description',
        },
        {
            text: `${lpg.unitprice}`, width: 100,
            src: 'unitprice',
        },
        {
            text: `${lpg.reserveqty1}`, width: 100,
            src: 'oneday',
        },
        {
            text: `${lpg.reserveqty2}`, width: 100,
            src: 'twoday',
        },
        {
            text: `${lpg.reserveqty3}`, width: 100,
            src: 'threeday',
        },
        {
            text: `${lpg.createtime}`, width: 100,
            src: 'createtime',
        },
        {
            text: `${lpg.status}`, width: 100,
            src: 'status',
            render:function (id, index, row) {
                let html = ``;
                if (row.status==='0') {
                    html += `<label class="apply">${lpg.statustype[0]}</label>`;
                }
                else if(row.status==='1') {
                    html += `<label class="agree">${lpg.statustype[1]}</label>`;
                }
                else if(row.status==='2') {
                    html += `<label class="deny">${lpg.statustype[2]}</label>`;
                }
                return html;
            },
        }
	],
	pagination: true,
});
refreshtable();


const addPage = __uri('/pages/logistics/cateringRental/cateringlist.html');

$("#btnServiceApply").click(function(){
    let param = $('#tbSearch').fieldsToJson();
    param.reservetype = "catering";
    let reservetype = `${param.reservetype}`;

        let pop = opg.popTop(`<iframe src="${url.setParam(addPage, {reservetype})}"></iframe>`, {
            title: `${lpg.serviceApplylist}`,
            btnMax: false,
            width: 900,
            height: 550,
            buttons: {
                send: {
                    className: 'btn-success',
                    text: lpg.sendApply,
                    onClick: function (i, ifr) {
                        let pop2=top.opg.confirm(lpg.submitinfo + "<br/>" + lpg.submitinfo2, function() {
                            let resource = ifr.getCateringFill();
                            if (resource)
                                sendCatering(resource, pop,pop2);

                        });
                        return true;
                    }
                },
                cancel: lpg.cancel
            },
            onClose: function () {

            }
        });


});

// $("#btnSearch").click(function(){
//     refreshtable();
// });

function sendCatering(resource, pop: PopUp,pop2:PopUp) {
	opg.api.addCatering(resource, function (data) {
		pop.close();
		pop2.close();
		refreshtable();
	});
}